
.m-operation button,
.m-operation a {
  min-width: 80px;
  margin-left: 10px;
}
.case-pic-list {
  background: #f5f5f5;
  padding: 20px 40px;
  position: relative;
  border-radius: 4px;
}
.case-cont {
  margin-top: 30px;
}
.case-cont-item {
  margin-bottom: 20px;
}
.case-cont-item::after {
  clear: both;
  content: "";
  display: block;
}
.case-cont-item .item-label {
  float: left;
  width: 100px;
  text-align: right;
  display: inline-block;
  margin-right: 20px;
}
.case-cont-item .item-info {
  float: left;
  max-width: 800px;
  border-radius: 4px;
}
.swiper-container {
  width: 100%;
  height: 230px;
  background: #f5f5f5;
}
.swiper-div {
  height: 100%;
  border: 2px solid #dddee1;
}
.swiper-active {
  height: 100%;
  border: 2px solid #00c5a3;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  cursor: pointer;
}
.swiper-slide img {
  width: 100%;
  height: 200px;
  display: block;
}
.swiper-slide span {
  display: block;
  text-align: center;
  font-size: 12px;
  line-height: 28px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.swiper-slide .u-add-btn {
  height: 100%;
  line-height: 228px;
  font-size: 14px;
  border: 1px dashed #999;
}
.swiper-btn-prev,
.swiper-btn-next {
  background-image: url('../../../../assets/images/icon-arrows.png');
  position: absolute;
  top: 50%;
  width: 23px;
  height: 37px;
  margin-top: -22px;
  z-index: 10;
  cursor: pointer;
}
.swiper-btn-prev {
  left: 8px;
}
.swiper-btn-next {
  right: 8px;
  background-position: -23px 0;
}



