
.sidebar-title {
  padding: 13px 0;
  color: #00c5a3;
  text-align: center;
  border-bottom: 1px solid #3a3d44;
}
.sidebar-logo {
  display: block;
  margin: 0 auto;
}
.sidebar-hide-text .sidebar-text {
  display: none;
}
.sidebar-hide-text .ivu-menu-item {
  text-align: center;
}
.sidebar-hide-text .ivu-menu-item > i {
  margin-right: 0;
}
.ivu-menu-vertical .menu-item.ivu-menu-item {
  padding: 0;
}
.ivu-menu-vertical .menu-item.ivu-menu-item p {
  padding: 14px 0 14px 45px;
}
.ivu-menu-vertical .submenu-item .ivu-menu-submenu-title {
  padding: 14px 24px;
}
.layout-mini .menu-item.ivu-menu-item p {
  padding: 14px 0;
  font-size: 12px;
}
.layout-mini .submenu-item .ivu-menu-submenu-title {
  padding: 14px 24px 30px 24px;
}



